<template>
  <div class="teamOrder mescroll">
    <div class="mid">
      <div class="mid-top">
        <div class="flex">
          <p class="left" @click="showDate = true">{{date}}</p>
          <img class="icon" src="../../assets/img/icon/down.png" alt @click="showDate = true" />
        </div>
        <div class="flex">
          <p class="flex-2">
            收入
            <span style="color:#eb5252">¥1234.56</span>
          </p>
          <p class="flex-2">
            冻结
            <span style="color:#000">¥1234.56</span>
          </p>
          <!-- <div class="flex-1 right">
            <p>| 月账单</p>
            <img src="../../assets/img/icon/left.png" alt />
          </div>-->
        </div>
      </div>
      <div class="list">
        <div class="item" v-for="item in 5" :key="item">
          <div class="item-pic flex-1">
            <img src="../../assets/img/default/defaultZ.png" />
          </div>
          <div class="flex-2">
            <div class="name">志高电热水壶 ZD1898</div>
            <div class="text">
              <span>下单用户</span>
              Andrew
            </div>
            <div class="text">
              <span>下单时间</span>
              2019.07.26 14:12:12
            </div>
            <div class="money">
              冻结佣金：¥
              <span>69.00</span>
            </div>
          </div>
        </div>
      </div>
    </div>
    <van-popup v-model="showDate" position="bottom">
      <van-datetime-picker
        v-model="currentDate"
        type="year-month"
        :formatter="formatter"
        @confirm="dateConfirm"
        @cancel="showDate = false"
      />
    </van-popup>
  </div>
</template>
<script>
import { Popup, DatetimePicker } from "vant";
export default {
  components: {
    [Popup.name]: Popup,
    [DatetimePicker.name]: DatetimePicker
  },
  data() {
    return {
      showDate: false,
      currentDate: new Date(),
      date: ""
    };
  },
  methods: {
    formatter(type, value) {
      if (type === "year") {
        return `${value}年`;
      } else if (type === "month") {
        return `${value}月`;
      }
      return value;
    },
    dateConfirm(value) {
      let year = new Date(value).getFullYear(),
        month = new Date(value).getMonth() + 1,
        day = new Date(value).getDate(),
        hour = new Date(value).getHours(),
        min = new Date(value).getMinutes();

      this.date = `${year}年${month < 10 ? "0" + month : month}月`;

      this.showDate = false;
    }
  },
  mounted() {
    document.title = "团队订单";
    this.date = `${new Date().getFullYear()}年${
      new Date().getMonth() + 1 < 10
        ? "0" + (new Date().getMonth() + 1)
        : new Date().getMonth() + 1
    }月`;
  }
};
</script>
<style lang="scss" scoped>
.teamOrder {
  .mid {
    margin-top: 15px;
    &-top {
      border-bottom: 1pt solid #efefef;
      padding-bottom: 10px;
      .flex {
        display: flex;
        padding: 5px 15px;
        align-items: center;
        .left {
          font-size: 18px;
          font-weight: bold;
        }
        .icon {
          width: 13px;
          margin-left: 5px;
        }
        .flex-2 {
          flex: 2;
          color: #666;
        }
        .right {
          width: 60px;
          display: flex;
          align-items: center;
          font-size: 13px;
          color: #999;
          img {
            width: 5px;
            margin-left: 5px;
          }
        }
      }
    }
    .list {
      padding: 0 15px;
      .item {
        padding: 10px 0;
        display: flex;
        border-bottom: 1pt solid #efefef;
        .item-pic {
          width: 120px;
          img {
            width: 100%;
            height: 120px;
          }
        }
        .flex-2 {
          padding: 0 0 0 15px;
          width: 60%;
        }
        .name {
          font-size: 15px;
          line-height: 25px;
          text-overflow: ellipsis;
          height: 25px;
          white-space: nowrap;
          overflow: hidden;
          width: 100%;
        }
        .text {
          font-size: 13px;
          line-height: 25px;
          color: #999;
          span {
            margin-right: 10px;
          }
        }
        .money {
          font-size: 15px;
          line-height: 25px;
          margin-top: 10px;
          color: $base-color;
          span {
            font-size: 18px;
          }
        }
      }
    }
  }
}
</style>
